
/* eslint-disable @typescript-eslint/no-explicit-any */
import Vue from 'vue'

export default Vue.extend({
  name: 'CustomSelect',
  props: {
    value: {
      type: Object,
      required: true,
    },
    placeholder: {
      type: String,
      default: '',
    },
    label: {
      type: String,
      default: '',
    },
  },
  data: () => ({
    opened: false,
  }),
  methods: {
    toggleOpen(value: boolean) {
      setTimeout(() => {
        this.opened = value
      }, 150)
    },
    selectObject(data: any) {
      this.$emit('input', data)
      this.opened = false
    },
  },
})
