
/* eslint-disable @typescript-eslint/no-explicit-any */
import Vue from 'vue'
import { mapActions, mapGetters } from 'vuex'

import CustomSelect from '@/components/Parts/CustomSelect.vue'

export default Vue.extend({
  name: 'JobStep3',
  components: {
    CustomSelect,
  },
  props: {
    question: {
      type: String,
      required: true,
    },
  },
  data: () => ({
    key: 'salary',
    inputPeriod: {
      key: '',
      title: '',
    },
    inputSalary: 0,
    salaryRange: {},
    periods: [
      {
        key: 'hourly',
        title: '$/hour',
      },
      {
        key: 'monthly',
        title: '$/month',
      },
      {
        key: 'yearly',
        title: '$/year',
      },
    ],
  }),
  mounted() {
    if (this.currentFilter) {
      this.inputPeriod = this.currentPeriod
      this.inputSalary = this.currentSalary
    }
  },
  methods: {
    ...mapActions(['addFilter', 'updateFilter']),
    selectFilter(data: any) {
      if (this.currentFilter)
        this.updateFilter({
          key: this.key,
          value: data,
        })
      else this.addFilter({ key: this.key, value: data })
    },
    setSalary() {
      const filter = this.currentFilter || {}
      filter.salary = this.inputSalary
      this.selectFilter(filter)
    },
    setPeriod() {
      const filter = this.currentFilter || {}
      filter.period = this.inputPeriod.key
      this.selectFilter(filter)
    },
  },
  computed: {
    ...mapGetters(['Filters']),
    currentFilter(): any {
      const index = this.Filters.findIndex(
        (filter: any) => filter.key == this.key,
      )
      if (index >= 0) return this.Filters[index].value
      else return undefined
    },
    currentSalary(): any {
      if (this.currentFilter.salary) return this.currentFilter.salary
      else return 0
    },
    currentPeriod(): any {
      if (this.currentFilter.period)
        return this.periods.find(
          (period: any) => period.key == this.currentFilter.period,
        )
      else
        return {
          key: 'hourly',
          title: '$/hour',
        }
    },
  },
  watch: {
    inputPeriod(newPeriod: any) {
      const ranges: any = {
        hourly: {
          min: 5,
          max: 100,
          step: 1,
        },
        monthly: {
          min: 50,
          max: 10000,
          step: 50,
        },
        yearly: {
          min: 1000,
          max: 100000,
          step: 500,
        },
      }
      const range = ranges[newPeriod.key]
      this.salaryRange = range
      this.inputSalary =
        this.inputSalary > range.max
          ? range.max
          : this.inputSalary < range.min
          ? range.min
          : this.inputSalary
      this.setPeriod()
    },
    inputSalary() {
      this.setSalary()
    },
  },
})
